* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
}

/* Navigation menu styles */
#nav-menu {
  background-color: #333;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  margin-right: 10px;
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #ffbb00;
}

/* Home section */
#home {
  background-color: #fff;
  padding: 40px;
  text-align: center;
}

#user-detail-name {
  font-size: 36px;
  margin-bottom: 10px;
}

#user-detail-intro {
  font-size: 18px;
  color: #777;
}

.home-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-top: 20px;
}

/* About section */
.about {
  background-color: #f5f5f5;
  padding: 40px;
}

.about h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

/* Skills section */
#skills {
  background-color: #fff;
  padding: 40px;
}

#skills h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.skills-card {
  display: inline-block;
  width: 200px;
  margin-right: 20px;
  text-align: center;
}

.skills-card-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.skills-card-name {
  margin-top: 10px;
}

/* Projects section */
#projects {
  background-color: #f5f5f5;
  padding: 40px;
}

#projects h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.project-card {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 4px;
}

.project-title {
  font-size: 18px;
  margin-top: 10px;
}

.project-description {
  color: #777;
  margin-top: 5px;
}

.project-tech-stack {
  color: #777;
  margin-top: 5px;
  font-size: 14px;
}

.project-github-link,
.project-deployed-link {
  display: block;
  margin-top: 10px;
}

/* Contact section */
#contact {
  background-color: #fff;
  padding: 40px;
  text-align: center;
}

#contact h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

#contact-github,
#contact-linkedin {
  display: inline-block;
  margin-right: 10px;
  color: #333;
  text-decoration: none;
  font-weight: bold;
}

#contact-phone,
#contact-email {
  margin-top: 10px;
  color: #777;
}

/* Resume section */
#resume-button-1 {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

/* Responsive styles */
@media only screen and (max-width: 768px) {
  /* Adjust the styles for smaller screens */

  /* Home section */
  #user-detail-name {
    font-size: 24px;
  }

  #user-detail-intro {
    font-size: 16px;
  }

  /* Skills section */
  .skills-card {
    width: 150px;
    margin-right: 10px;
  }

  .skills-card-img {
    width: 60px;
    height: 60px;
  }

  .skills-card-name {
    margin-top: 5px;
  }
}

/* Add animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Apply animations */
.home-img,
.skills-card,
.project-card {
  animation: fadeIn 1s ease-in-out;
}

#home,
#about,
#skills,
#projects,
#contact {
  animation: slideInFromLeft 1s ease-in-out;
}